
.header {
  width: 100%;
  height: 44px;
  line-height: 44px;
  text-align: center;
  font-size: 0;
}
.header .icon {
    display: inline-block;
    vertical-align: middle;
    width: 30px;
    height: 32px;
    margin-right: 9px;
    background-image: url("logo@2x.png");
    background-size: 30px 32px;
}
@media (-webkit-min-device-pixel-ratio: 3), (min-device-pixel-ratio: 3) {
.header .icon {
        background-image: url("logo@3x.png");
}
}
.header .text {
    display: inline-block;
    vertical-align: top;
    font-size: 18px;
    color: #ffcd32;
}



