
.recommend {
  position: fixed;
  top: 88px;
  bottom: 0;
  width: 100%;
  overflow: hidden;
}
.recommend .recommend-content {
    position: relative;
}
.recommend .recommend-content .img-swiper {
      margin-bottom: 14px;
}
.recommend .recommend-content .img-swiper .swiper-pagination-bullet-active {
        background-color: #ffcd32;
}
.recommend .recommend-content .radio-wrapper {
      margin: 0 10px 7px;
      text-align: center;
      font-size: 16px;
      color: #ffcd32;
}
.recommend .recommend-content .radio-wrapper .title {
        margin-bottom: 10px;
}
.recommend .recommend-content .radio-wrapper .radio-list {
        overflow: hidden;
        margin-right: -8px;
}
.recommend .recommend-content .radio-wrapper .radio-list .radio-item {
          float: left;
          width: 48%;
          padding-right: 2%;
          margin-bottom: 10px;
          overflow: hidden;
}
.recommend .recommend-content .radio-wrapper .radio-list .radio-item .radio-img {
            position: relative;
}
.recommend .recommend-content .radio-wrapper .radio-list .radio-item .radio-img img {
              position: absolute;
              top: 0;
              left: 0;
              display: block;
              width: 100%;
              z-index: 1;
}
.recommend .recommend-content .radio-wrapper .radio-list .radio-item .radio-img:before {
              content: "";
              display: block;
              padding-top: 100%;
}
.recommend .recommend-content .radio-wrapper .radio-list .radio-item .name {
            margin-top: 5px;
}
.recommend .recommend-content .hot-song-list .title {
      margin-bottom: 10px;
      text-align: center;
      font-size: 16px;
      color: #ffcd32;
}
.recommend .recommend-content .hot-song-list .song-list {
      display: -webkit-box;
      display: -ms-flexbox;
      display: flex;
      padding: 0 10px 10px;
}
.recommend .recommend-content .hot-song-list .song-list .icon {
        -webkit-box-flex: 0;
            -ms-flex: 0 0 60px;
                flex: 0 0 60px;
        width: 60px;
        margin-right: 20px;
}
.recommend .recommend-content .hot-song-list .song-list .text {
        -webkit-box-flex: 1;
            -ms-flex: 1;
                flex: 1;
        font-size: 14px;
}
.recommend .recommend-content .hot-song-list .song-list .text .name {
          height: 30px;
          line-height: 30px;
          color: #fff;
}
.recommend .recommend-content .hot-song-list .song-list .text .desc {
          height: 30px;
          line-height: 30px;
          text-overflow: ellipsis;
          white-space: nowrap;
          overflow: hidden;
          color: rgba(255, 255, 255, 0.5);
}
.recommend .recommend-content .hot-song-list .song-list:last-child {
        padding-bottom: 30px;
}
.recommend .recommend-content .top-tip {
      position: absolute;
      top: -60px;
      left: 0;
      width: 100%;
      height: 60px;
      line-height: 60px;
      text-align: center;
      font-size: 14px;
      color: #fff;
}
.recommend .recommend-content .bottom-tip {
      position: absolute;
      bottom: 10px;
      width: 100%;
      text-align: center;
      font-size: 12px;
      color: rgba(255, 255, 255, 0.5);
}



